'use client';

import { useState, useEffect, useRef } from 'react';
import MessageList from './MessageList';
import InputBox from './InputBox';

interface Message {
  role: 'user' | 'assistant';
  content: string;
  audio_url?: string;
}

interface ChatWindowProps {
  sessionId: string;
}

export default function ChatWindow({ sessionId }: ChatWindowProps) {
  const [messages, setMessages] = useState<Message[]>([]);
  const [loading, setLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Fetch chat history when sessionId changes
  useEffect(() => {
    fetchHistory();
  }, [sessionId]);

  // Scroll to bottom when messages change
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const fetchHistory = async () => {
    try {
      const res = await fetch(`http://localhost:5000/chat/history/${sessionId}`);
      const data = await res.json();
      setMessages(data);
    } catch (error) {
      console.error('Failed to fetch history:', error);
    }
  };

  const handleSendMessage = async (text: string) => {
    setLoading(true);
    try {
      const res = await fetch(`http://localhost:5000/chat/${sessionId}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ message: text, mode: 'text' }),
      });
      const data = await res.json();

      if (data.message) {
        setMessages([
          ...messages,
          { role: 'user', content: text },
          { role: 'assistant', content: data.message, audio_url: data.audio_url },
        ]);
      }
    } catch (error) {
      console.error('Failed to send message:', error);
    }
    setLoading(false);
  };

  const handleAudioSubmit = async (audioBlob: Blob) => {
    setLoading(true);
    try {
      const reader = new FileReader();
      reader.onload = async () => {
        const base64 = (reader.result as string).split(',')[1];
        const res = await fetch(`http://localhost:5000/audio/interact/${sessionId}`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ audio: base64 }),
        });
        const data = await res.json();

        if (data.assistant_said) {
          setMessages([
            ...messages,
            { role: 'user', content: data.user_said },
            { role: 'assistant', content: data.assistant_said, audio_url: data.audio_url },
          ]);
        }
      };
      reader.readAsDataURL(audioBlob);
    } catch (error) {
      console.error('Failed to send audio:', error);
    }
    setLoading(false);
  };

  return (
    <div className="flex flex-col h-full max-h-[600px] bg-slate-800/30 backdrop-blur-sm rounded-2xl border border-slate-700 shadow-2xl overflow-hidden">
      {/* Messages */}
      <div className="flex-1 overflow-y-auto">
        <MessageList messages={messages} />
        <div ref={messagesEndRef} />
      </div>

      {/* Input Area */}
      <div className="border-t border-slate-700 p-4 bg-slate-900/50">
        <InputBox
          onSendMessage={handleSendMessage}
          onAudioSubmit={handleAudioSubmit}
          loading={loading}
        />
      </div>
    </div>
  );
}